import { CallService } from "./../../services/call.service";
import { CallComponent } from "./../../common/call/call.component";
import { SharedResourceUpdateStatus } from "./../../architecture/shared-resource-async-request-bundle";
import { SessionsManagerService } from "src/app/services/sessions-manager-service";
import { DepositComponent } from "./../../common/deposit/deposit.component";
import { UserService } from "src/app/services/user.service";
import { FileModel, FileFetchStatus } from "./../../models/file-model";
import { AdditionalInfo } from "./../../models/additional-info";
import { AdditionalInfoEditComponent } from "./../../common/additional-info-edit/additional-info-edit.component";
import { AuthService } from "src/app/services/auth.service";
import { Provider } from "src/app/models/provider";
import { AlertService } from "./../../services/alert.service";
import {
  NavController,
  ModalController,
  ActionSheetController,
  AlertController,
  LoadingController,
} from "@ionic/angular";
import { EnvService } from "./../../services/env.service";
import { User } from "./../../models/user";
import { ProviderService } from "./../../services/provider.service";
import { Component, OnInit } from "@angular/core";
import {
  map,
  concatMap,
  catchError,
  takeUntil,
  mergeMap,
  take,
} from "rxjs/operators";
import { ActivatedRoute } from "@angular/router";
import { from, of, Subject, merge, forkJoin, zip } from "rxjs";
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: "app-usr-provider-single",
  templateUrl: "./usr-provider-single.page.html",
  styleUrls: ["./usr-provider-single.page.scss"],
})
export class UsrProviderSinglePage implements OnInit {
  

  public getName = User.getName;
  public getStatusText = Provider.getStatusText;
  verifiedText;
  isAdmin = false;
  constructor(
    public providerService: ProviderService,
    public env: EnvService,
    public sessionsManagerService: SessionsManagerService,
    private navController: NavController,
    private alert: AlertService,
    private route: ActivatedRoute,
    private auth: AuthService,
    private modalController: ModalController,
    private actionSheet: ActionSheetController,
    private userService: UserService,
    private sessionsManager: SessionsManagerService,
    private loadingModal: LoadingController,
    private callService: CallService
  ) {}

  loadProvider() {
    var providerId = this.route.snapshot.params.providerId;
    if (
      this.providerService.selectedProviderShared.resource() &&
      this.providerService.selectedProviderShared.resource().id != providerId
    ) {
      this.providerService.selectedProviderShared.reset();
    }
    this.providerService.selectedProviderShared
      .getUpdateNotifier(this)
      .subscribe((provider) => {
        console.log('notifier');
         this.providerService.selectedProviderShared.resource();
        let cert = Provider.getCertificateFileName(this.providerService.selectedProviderShared.resource());

        if (cert) {
          this.providerService.selectedProviderShared.resource().certificateFile = new FileModel();
          this.providerService.selectedProviderShared.resource().certificateFile.name = cert;
          this.providerService.downloadCertificate(this.providerService.selectedProviderShared.resource()).subscribe();
        }
        this.setAdminParams();
      });
    this.providerService.selectedProviderShared.request(providerId);
    
  }
  isCertificateDownloading() {
    return (
      this.providerService.selectedProviderShared.resource().certificateFile.fetch_status ==
      FileFetchStatus.FILE_FETCH_STATUS_FETCHING
    );
  }
  certificateDownloadProgress() {
    return (this.providerService.selectedProviderShared.resource().certificateFile.fetch_progress / 100).toPrecision(2);
  }
  ionViewWillEnter() {
    this.loadProvider();
  }
  setAdminParams() {
    this.verifiedText = Provider.getVerifiedByAdminText(this.providerService.selectedProviderShared.resource());
    if (!this.providerService.selectedProviderShared.resource().user.additional_info) {
      let additionalInfo = new AdditionalInfo();
      additionalInfo.postal_code = "";
      additionalInfo.national_code = "";
      additionalInfo.land_line_number = "";
      additionalInfo.address = "";
      this.providerService.selectedProviderShared.resource().user.additional_info = additionalInfo;
    }
  }
  isOnline() {
    return this.sessionsManagerService.isUserOnline(this.providerService.selectedProviderShared.resource().user);
  }
  isContact() {
    return this.sessionsManagerService.isUserInContacts(this.providerService.selectedProviderShared.resource().user);
  }
  requestSession() {
    // this.navController.navigateForward([
    //   "/usr-view-provider-calendar/" + this.provider.id,
    // ]);
    this.navController.navigateForward([
      "/usr-select-session-type/" + this.providerService.selectedProviderShared.resource().id,
    ]);
    /* this.sessionsManagerService.usrRequestSession(this.provider.id).then(
      session=>{
        if(session!=null)
        {
          this.alert.presentToast(session.chat_topic_name);
        }
        
        else this.alert.presentToast('already waiting approval..');
      }
    ); */
  }
  initiateStartSession() {
    zip(
      this.sessionsManager.selectedProviderReservedSessionsShared.getResourceReadyNotifier(),
      this.sessionsManager.myCurrentSessionsShared.getResourceReadyNotifier()
    )
      .pipe(take(1))
      .subscribe((result) => {
        this.loadingModal.dismiss();
        this.alert.openSessionTypesActionSheet(this.startSession, this);
      });
    this.sessionsManager.myCurrentSessionsShared.request();
    this.sessionsManager.selectedProviderReservedSessionsShared.request(
      this.providerService.selectedProviderShared.resource().id
    );
    this.alert.presentLoadingModal(this.loadingModal).subscribe();
  }
  canStartSession() {
    return (
      this.providerService.selectedProviderShared.getResourceStatus() ==
      SharedResourceUpdateStatus.UPDATED
    );
  }
  startSession(duration: number, isTextSession) {
    if (
      this.sessionsManager.hasEnoughBalanceForSessionRequest(
        this.providerService.selectedProviderShared.resource(),
        isTextSession,
        duration
      )
    ) {
    }
  }
  addCancelButton(buttons) {
    (buttons as any).push({
      text: "انصراف",
      role: "cancel",
    });
  }

  openAdditionalInfoEdit() {
    const props = {
      additionalInfo: this.providerService.selectedProviderShared.resource().user.additional_info,
      editable: false,
    };
    this.alert.presentModal(AdditionalInfoEditComponent, props).subscribe();
    //this.presentAdditionalInfoModal().subscribe();
  }

  canRequestSession() {
    return this.sessionsManagerService.isContactListReady(); // && this.isContact();
  }
  toggleFavorite(provider) {
    this.env.somethingIsPending("toggling favorite provider");
    this.providerService.toggleFavorite(provider.id).subscribe((result) => {
      this.providerService.favoriteProvidersShared.request();
      this.env.somethingIsDonePending("toggling favorite provider");
    });
  }
  verifyProvider() {
    this.env.somethingIsPending("verifying provider's account");
    this.providerService
      .verifyProvider(this.providerService.selectedProviderShared.resource())
      .pipe(
        catchError((error) => {
          this.alert.presentError("تأیید مشاور با مشکل مواجه شد.");
          return of(false);
        })
      )
      .subscribe((result) => {
        this.env.somethingIsDonePending("verifying provider's account");
        if (result) {
          this.alert.presentSuccess("تأیید مشاور با موفقیت انجام شد.");
          this.providerService.selectedProviderShared.resource().verified_by_admin = true;
          this.verifiedText = Provider.getVerifiedByAdminText(this.providerService.selectedProviderShared.resource());
        }
      });
  }
  startChat() {}
  ngOnInit() {
    this.auth.isAdmin().then((result) => {
      this.isAdmin = result;
    });
    //this.sessionsManager.myCurrentSessionsShared.request();
  }
  ngOnDestroy(): void {
    
  }
}
